import React, { useState } from 'react';
import s from './Photophilter.module.css';
import { NavLink} from 'react-router-dom';
import Overlayslider from './overlayslider/Overlayslider.js';
import Shooter from './shooter/Shooter.js';




const Photophilter = () => {
  const [image, setImage] = useState({ preview: "", raw: "" });

  const handleChange = e => {
    if (e.target.files.length) {
      setImage({
        preview: URL.createObjectURL(e.target.files[0]),
        raw: e.target.files[0]
      });
    }
  };
  
  const handleUpload = async e => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("image", image.raw);
  
    await fetch("/photophilter", {
      method: "POST",
      headers: {
        "Content-Type": "multipart/form-data"
      },
      body: formData
    });
  };

    return (
      <section>
          <div className={s.frame_wrapper}>
          
            {image.preview ? (
              <img src={image.preview} alt="dummy" width="100%" height="100%" />
            ) : (
              ''
            )}
            <div className={s.frame_wrapper_overlay} id="frame_wrapper_overlay">
            </div>
          
          </div>
          <h3>Create a shot</h3>
          <Shooter />
          <h3>Choose a frame</h3>
          <Overlayslider />
          <input
            type="file"
            id="upload-button"
            style={{ display: "none" }}
            onChange={handleChange}
          />
          <label htmlFor="upload-button">
          <h5 className={s.uploadlink}>Upload your photo</h5>
          </label>
          {/* <button onClick={handleUpload}>Upload</button> */}
          <nav className={s.mainNav}>
            <NavLink to="/appmenu">
                Return to menu
            </NavLink>
          </nav>
      </section>
    );
  }

export default Photophilter;